<template>
	<view style="width: 100%;height: 100%;">
		<view class="container" v-show="showPageOne">
			<view class="mymap" id="mymap">			
			</view>
			<view class="rightToll">
				<view class="tollone">
					<view class="uni-list" v-show="!layersFrame">
						<radio-group @change="radioChange">
							<label class="uni-list-cell" v-for="(item, index) in radioitems" :key="item.value">
								<view>
									<radio :value="item.value" :checked="index === current" />
								</view>
								<view>{{item.name}}</view>
							</label>
						</radio-group>
					</view>
					<view class="layersControl tollIcon" @click="layerframeControl"></view>
				</view>
				<view class="tolltwo">
					<view class="uni-list" v-show="!datasFrame">
						<checkbox-group @change="checkboxChange">
							<label class="uni-list-cell" v-for="item in checkboxitems" :key="item.value">
								<view>
									<checkbox :value="item.value" :checked="item.checked" />
								</view>
								<view>{{item.name}}</view>
							</label>
						</checkbox-group>
					</view>
					<view class="datassControl tollIcon" @click="datasframeControl"></view>
				</view>
				<view class="tollthree">
					<view class="localControl tollIcon" @click="localtionControl"></view>
				</view>
				<view class="tollfour" v-show="!showPageThree">
					<view class="helpControlerr tollIcon" @click="helpControlerr"></view>
				</view>
				<view class="tollsix" v-show="showPageThree">
					<view class="clearControl tollIcon" @click="drawEventClear"></view>
				</view>
				<view class="tollfive" v-show="showPageThree">
					<view class="pencilControl tollIcon" @click="drawEvent"></view>
				</view>
				
			</view>
			<view class="bottomBar" v-show='!popupShow'>
				<view class="" v-show="showPageBars">
					<view class="bottomBtns bottomBtns1" @click="newaddBtn">				
					</view>
					<span >新增</span>
				</view>
				<view class="" v-show="showPageBars">
					<view  class="bottomBtns bottomBtns2" @click="dismantleBtn">
					</view>
					<span>拆除</span>
				</view>
				<view class="" v-show="showPageBars">
					<view class="bottomBtns bottomBtns3" @click="reformBtn">					
					</view>
					<span >改造</span>
				</view>
				<view v-show="!showPageBars">
					<button type="primary" size="mini" @click="backContent" class="backContent">保存</button>
				</view>
			</view>
		</view>
		<!-- 修改本图形 -->
		<view class="modifyOwn" v-show="popupShow">修改</view>
		<!-- dierye -->
		<view class="bhfxIndex" v-show="!showPageOne">
			<view class="phototitle">拍照</view>
			<view class="cameraDiv">
				<view class="cameraChild" @click="cameraChild(index)" v-for="(item,index) in formdata.imgfiles">
					<image :src="item.imagesrc" :key="item.id" mode="scaleToFill" ref="cameraImg" class="cameraImg"/>
				</view>
			</view>
			<view class="formDiv">
				<b>上传时间：</b>{{formdata.SUNTIME}}
			</view>
			<!-- <view class="formDiv">
				<b>拍照地点：</b>{{formdata.bhlocation}}
			</view> -->
			<view class="formDiv formDiv2" style="border-bottom: none;" v-show='!errShow'>
				<view class="title"><b>变化类型：</b></view>
				<radio-group >
					<label v-for="(item,index) in radios" class="radioLabel">
						<radio :value="item.radioval" :key="item.id" :checked="index==current" :disabled="index!==current"  /><text :class="{'radioColor':index!==current}">{{item.radioval}}</text>
					</label>
				</radio-group>
			</view>
			<view class="formDiv formDiv2" style="border-bottom: none;" v-show='errShow'>
				<view class="title"><b>变化类型：</b></view>
				<radio-group @change="radioerrType">
					<label v-for="(item,index) in radios" class="radioLabel">
						<radio :value="item.radioval" :key="item.id" /><text>{{item.radioval}}</text>
					</label>
				</radio-group>
			</view>
			<view class="formDiv" style="padding-left: 80px;">
				前期：
				<select  @change="selectOne" id="selectOne">
				  <option value =""></option>
				  <option value ="建筑">建筑</option>
				  <option value ="道路">道路</option>
				  <option value="绿地">绿地</option>
				  <option value="裸地">裸地</option>
				  <option value="绿网">绿网</option>
				</select>
				后期：
				<select  @change="selectTwo" id="selectTwo">
				  <option value =""></option>
				  <option value ="建筑">建筑</option>
				  <option value ="道路">道路</option>
				  <option value="绿地">绿地</option>
				  <option value="裸地">裸地</option>
				  <option value="绿网">绿网</option>
				</select>
			</view>
			<view class="formDiv" @click="drawRange">
				<b class="drawIcon">绘制范围：</b>
			</view>
			<view class="formDiv">
				<view><b>变化详情：（大于15字）</b></view>
				<textarea  placeholder="请输入您的详细描述" class="formTextarea" @blur="getTextcontent"/>
			</view>
			<view>
				<button type="primary" @click="submitImgs">提交</button>
			</view>
		</view>
		
	</view>
</template>

<script>
	var _this;
	import esriLoader from 'esri-loader';
	import {appLoginWx,request,request2} from '@/pages/network/appLoginWx.js'
		export default {
			data() {
				return {
					map:null,
					showLayer:true,
					layersFrame:true,
					datasFrame:true,
					radioitems: [
						{
							value: 'imgMap',
							name: '卫星地图',
							checked: 'true'
						},
						{
							value: 'skyMap',
							name: '天地图'							
						},
					],
					checkboxitems: [
						{
							value: 'poi',
							name: 'POI',
							checked: 'true'
						},
						{
							value: 'house',
							name: '房屋'							
						},
						{
							value: 'road',
							name: '道路'							
						},
					],
					current: 0,
					longitudeData:0,
					latitudeData:0,
					resconfig:null,
					view:null,
					symbol:null,
					renderer:null,
					formdata:{
						imgfiles:[
							{
								imagesrc:"data:image/jpeg;base64,",
								id:0,
							},
							{
								imagesrc:"data:image/jpeg;base64,",
								id:1,
							},
							{
								imagesrc:"data:image/jpeg;base64,",
								id:2,
							}
						],
						SUNTIME:"2020",						
						BHTYPE:"拆除",
						BHREMARK:"",						
						BHBEFORE:'绿地',
						BHAFTER:'裸地',
						// bhlocation:'经纬度',	
						OLDID:0,//旧Id
						addId:0,
					},				
					radios:[
						{
							id:0,
							radioval:"新增"
						},
						{
							id:1,
							radioval:"拆除"
						},
						{
							id:2,
							radioval:"改造"
						},
					],
					current:0,
					showPageOne:true,
					showPageThree:false,
					showPageBars:true,
					popupShow:false,
					errShow:false,
					draw:null,
					addGraphic:null,
					imgfilesNew:[
						"https://www.sunset.com/wp-content/uploads/96006df453533f4c982212b8cc7882f5-800x0-c-default.jpg",
						"https://www.sunset.com/wp-content/uploads/96006df453533f4c982212b8cc7882f5-800x0-c-default.jpg",
						"https://www.sunset.com/wp-content/uploads/96006df453533f4c982212b8cc7882f5-800x0-c-default.jpg"
					],
				}
			},
			mounted() {				
			},
			beforeDestroy(){
				if(this.view){
					this.view.container = null;
				}
			},
			onShow(){
				_this=this;
				_this.$jweixin.getLocation({
					type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
					  success: function (res) {
						var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
						var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
						// var speed = res.speed; // 速度，以米/每秒计
						// var accuracy = res.accuracy; // 位置精度
						_this.longitudeData=Number(longitude);
						_this.latitudeData=Number(latitude );
						_this.creatMapview();//创建地图
					  },
					  fail:function(err){
						console.log(JSON.stringify(err))
					  }
				})
			},
			methods: {
				creatMapview(){
					esriLoader.loadModules(['esri/Map','esri/views/MapView','esri/layers/FeatureLayer',"esri/layers/TileLayer","esri/layers/WebTileLayer",
					"esri/views/draw/Draw","esri/layers/GraphicsLayer","esri/layers/MapImageLayer","esri/PopupTemplate"],{
						css:true
					})
					.then(([Map,MapView,FeatureLayer,TileLayer,WebTileLayer,Draw,GraphicsLayer,MapImageLayer,PopupTemplate])=>{
						let map = new Map({});
						this.map=map;
						this.view = new MapView({
							container:'mymap',
							map:map,
							center:[this.longitudeData,this.latitudeData],
							zoom:8,
							// zoom:5,
						});
						this.view.ui._removeComponents(['zoom']);//去掉缩放图标
						this.view.ui._removeComponents(["attribution"]);//去掉ersilogo
						//draw工具
						this.draw = new Draw({
						  view: this.view
						});
						let imageLayer2=new MapImageLayer({
							url:"http://bhfx.hanwuqi.xyz/arcgis/rest/services/GuoTuDuCha/GTDC/MapServer",							
						  });
						map.add(imageLayer2);//模拟天地
						//影像切片
						let imageLayer=new TileLayer({
							//影像服务"esri/layers/ImageryLayer"
							// url: "https://landsat2.arcgis.com/arcgis/rest/services/Landsat8_Views/ImageServer"							
							//MapServer"结尾:"esri/layers/MapImageLayer"动态服务，无切片信息；
							//MapServer"结尾:esri/layers/TileLayer"或者"esri/layers/WebTileLayer"切片服务有切片信息 	
							// url:"https://elevation3d.arcgis.com/arcgis/rest/services/NatGeo_World_Map/MapServer"
							url:"http://bhfx.hanwuqi.xyz/arcgis/rest/services/2019_HP/MapServer",
							visible:false
						  });
						map.add(imageLayer);//影像切片图
						// let webTile=new WebTileLayer({
						//    urlTemplate: 'https://stamen-tiles-{subDomain}.a.ssl.fastly.net/terrain/{level}/{col}/{row}.png',
						//    subDomains: ['a', 'b', 'c', 'd'],
						//    visible:false
						//  });
						// map.add(webTile);//天地图						
						// 坐标标记
						this.symbol = {
						   type: "picture-marker",  // autocasts as new PictureMarkerSymbol()
						   url: "data:image/svg+xml;base64,",
						   width: "20px",
						   height: "20px"
						 };
						const graphics = [{
							geometry: {
								type: 'point',
								longitude: _this.longitudeData, // 经度116.29845,39.95933
								latitude: _this.latitudeData, // 纬度
							}
						}];
						  // 设置渲染
						_this.renderer = {
							type: 'simple',
							symbol:_this.symbol,
						 };
						  // 创建要素图层
						const marklayer = new FeatureLayer({
							source: graphics,
							objectIdField: 'ObjectID',
							renderer:this.renderer,
						});
						map.add(marklayer);//添加标注
						// featruesLayer
						let template={
							// title: function(event) {return "{OBJECTID}";},
							content: [{
									type: "fields",
									fieldInfos: [
										{
										fieldName: "OBJECTID",
										label: "唯一码",
										type: "number",
									},{
										fieldName: "BHTYPE",
										label: "变化类型",
										format: {
											places: 0,
											digitSeparator: true
										}
									},{
										fieldName: "SUNTIME",
										label: "提交时间",
										type: "string"
									}, {
										fieldName: "BHBEFORE",
										label: "变化前"										
									},{
										fieldName: "BHAFTER",
										label: "变化后"								
									}, {
										fieldName: "BHREMARK",
										label: "变化详情",
										type: "string"
									},]
								},
								{ // Autocasts as new MediaContent()
								  type: "media",
								  mediaInfos: [ {
								    // title: "<b>现场图片</b>",
								    type: "image",
								    value: {
								      sourceURL:_this.imgfilesNew[0]
								    }
								  },
								  {
								    // title: "<b>现场图片</b>",
								    type: "image",
								    value: {
								      sourceURL:_this.imgfilesNew[1]
								    }
								  },{
								    // title: "<b>现场图片</b>",
								    type: "image",
								    value: {
								      sourceURL:_this.imgfilesNew[2]
								    }
								  },]
								},]
						}
						const layerfeaturePoi = new FeatureLayer({
						  // url: 'https://sampleserver6.arcgisonline.com/arcgis/rest/services/USA/MapServer/0',
						  url:"http://192.168.1.101:6080/arcgis/rest/services/BianHuaFaXianWX/FaXianBianHuaWX/FeatureServer/0",
						  // visible:false,
						  outFields: ["*"],
						  popupTemplate: template
						});
						layerfeaturePoi.popupTemplate.overwriteActions = true;//zoom to按钮给去除
						const layerfeatureHouse = new FeatureLayer({
						  // url: 'https://sampleserver6.arcgisonline.com/arcgis/rest/services/USA/MapServer/1',
						   url:"http://192.168.1.101:6080/arcgis/rest/services/BianHuaFaXianWX/FaXianBianHuaWX/FeatureServer/0",
						  visible:false
						});
						const layerfeatureRoad = new FeatureLayer({
						  // url: 'https://sampleserver6.arcgisonline.com/arcgis/rest/services/USA/MapServer/2',
						   url:"http://192.168.1.101:6080/arcgis/rest/services/BianHuaFaXianWX/FaXianBianHuaWX/FeatureServer/0",
						  visible:false
						});
						map.addMany([layerfeaturePoi,layerfeatureHouse,layerfeatureRoad]);
						_this.view.on('click',function(event){
							let screenPoint={
								x:event.x,
								y:event.y
							}
							if(_this.showPageBars){							
								_this.view.hitTest(screenPoint).then(function(response){
									if(response.results.length>0){
										let id=response.results[0].graphic.attributes.OBJECTID;//获取要查看属性图斑的的id（原图形的id）
										_this.OLDID=id;
										_this.popupShow=false;
										let esriPopup=document.querySelector('.esri-popup');
										esriPopup.style.display='none';
										//请求后台获取图片
										request2({
											url:'/api/bhfx/getphotos',
											header: {'Authorization':uni.getStorageSync('token')},
											data: {
												objectId:id
											},
										}).then((res)=>{
											if(res.data.Status=="success"){//点击图形更改图片路径
												let dataArr=res.data.Data;
												dataArr.forEach((item,index)=>{
													_this.map.layers.items[3].popupTemplate.content[1].mediaInfos[index].value.sourceURL="http://192.168.1.120:56273"+item;
												});												
											}											
										})
										setTimeout(function(){
											_this.$nextTick(function(){
												_this.popupShow=true;											
												esriPopup.classList.remove('esri-popup--is-docked');
												esriPopup.style.display='flex';
												esriPopup.style.bottom='0px';
												let esriClose=document.querySelector('.esri-popup__header-buttons');
												let esriPrevious=document.querySelector('.esri-popup__pagination-previous');
												let esriNext=document.querySelector('.esri-popup__pagination-next');
												let esriImgPrevious=document.querySelector('.esri-feature-media__previous');
												let esriImgNext=document.querySelector('.esri-feature-media__next');												
												if(esriClose){
													esriClose.addEventListener('click',function(){
														_this.popupShow=false;
													});																										
												}else{
													_this.popupShow=true;
												}
												if(esriPrevious){
													esriPrevious.addEventListener('click',function(){
														_this.popupPosition();
													});													
												}
												if(esriNext){
													esriNext.addEventListener('click',function(e){
														debugger
														_this.popupPosition();
													});
												}
												if(esriImgPrevious){
													esriImgPrevious.addEventListener('click',function(){
														esriPopup.style.display='none';
														setTimeout(function(){
															esriPopup.style.setProperty("bottom","0px","important");
															esriPopup.style.display='flex';
														},100)
													});													
												}
												if(esriImgNext){
													esriImgNext.addEventListener('click',function(){
														esriPopup.style.display='none';
														setTimeout(function(){
															esriPopup.style.setProperty("bottom","0px","important");
															esriPopup.style.display='flex';
														},100)
													});
												}
											})
										},1000)
									}else{
										_this.popupShow=false;
									}									
								})								
							}
						})	
						this.view.when(()=>{
							let esriPopup=document.getElementsByClassName('esri-popup')[0];
							// esriPopup.classList.add("active");
							esriPopup.style.width='100%';
							esriPopup.style.height=248 + 'px';//165
							// esriPopup.style.bottom=0 +'px!important';
							esriPopup.style.setProperty("bottom","0px","important");
							// let esriUi=document.querySelector('.esri-ui');
							// esriUi.style.marginBottom='-124px';
						})
					})
				},
				popupPosition(){
					let esriPopup=document.querySelector('.esri-popup');					
					esriPopup.style.display='none';
					setTimeout(function(){						
						_this.$nextTick(function(){
							let esriPopupId=document.querySelector('.esri-feature-fields__field-data').innerText;debugger
							//请求后台获取图片
							request2({
								url:'/api/bhfx/getphotos',
								header: {'Authorization':uni.getStorageSync('token')},
								data: {
									objectId:esriPopupId
								},
							}).then((res)=>{debugger
								if(res.data.Status=="success"){//点击图形更改图片路径
									let dataArr=res.data.Data;
									dataArr.forEach((item,index)=>{
										_this.imgfilesNew[index]="http://192.168.1.120:56273"+item;
										_this.map.layers.items[3].popupTemplate.content[1].mediaInfos[index].value.sourceURL="http://192.168.1.120:56273"+item;
									
									});												
								}											
							})			
							esriPopup.style.setProperty("bottom","0px","important");
							setTimeout(function(){
								let esriImgPrevious=document.querySelector('.esri-feature-media__previous');
								let esriImgNext=document.querySelector('.esri-feature-media__next');
								if(esriImgPrevious){
									esriImgPrevious.addEventListener('click',function(){
										esriPopup.style.display='none';
										setTimeout(function(){
											esriPopup.style.setProperty("bottom","0px","important");
											esriPopup.style.display='flex';
										},100)
									});													
								}
								if(esriImgNext){
									esriImgNext.addEventListener('click',function(){
										esriPopup.style.display='none';
										setTimeout(function(){
											esriPopup.style.setProperty("bottom","0px","important");
											esriPopup.style.display='flex';
										},100)
									});
								}
							},1000)
						});
						esriPopup.style.display='flex';
					},100)
					
					// esriPopup.style.display='none';
					// _this.popupShow=false;
					// setTimeout(function(){
					// 	esriPopup.style.bottom='0px';
					// 	esriPopup.style.display='flex';
					// 	_this.popupShow=true;
					// },1000)
				},
				radioChange(evt) {
					if(evt.target.value=="imgMap"){
						this.map.layers.items[0].visible=this.showLayer
						this.map.layers.items[1].visible=!this.showLayer
					}else{
						this.map.layers.items[0].visible=!this.showLayer
						this.map.layers.items[1].visible=this.showLayer
					}
				},
				layerframeControl(){
					this.layersFrame=!this.layersFrame;
				},
				checkboxChange(e) {
					let items = this.checkboxitems;
					let	values = e.detail.value;
					for (let i = 0, lenI = items.length; i < lenI; ++i) {
						const item = items[i]
						if(values.includes(item.value)){
							this.$set(item,'checked',true);
							this.map.layers.items[3+i].visible = true;
						}else{
							this.$set(item,'checked',false);
							this.map.layers.items[3+i].visible = false;
						}
					}
				},
				getTextcontent(e){
					let	values = e.detail.value;
					this.formdata.BHREMARK=values;
				},
				datasframeControl(){
					this.datasFrame=!this.datasFrame
				},
				localtionControl(){					
					// this.$jweixin.miniProgram.getEnv(function(res){
						_this.$jweixin.getLocation({
							type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
							  success: function (res) {
							    var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
							    var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
							    var speed = res.speed; // 速度，以米/每秒计
							    var accuracy = res.accuracy; // 位置精度								
								_this.longitudeData=-Number(longitude);
								_this.latitudeData=Number(latitude);
								let updatelayer=_this.map.layers.items[2];//标记图层
								_this.view.center=[_this.longitudeData,_this.latitudeData];
								const updateEdit={
										updateFeatures:[{
											"geometry":{
												type: 'point',
												longitude: _this.longitudeData, // 经度116.29845,39.95933
												latitude: _this.latitudeData, // 纬度
											},
											"attributes":{
											   "ObjectID": updatelayer.source.items[0].attributes.ObjectID,
											}
										}]
									}								
								updatelayer.applyEdits(updateEdit)
								.then(function(editsResult){
									console.log(editsResult)
								})								
							  },
							  fail:function(err){
							  	console.log(JSON.stringify(err))
							  }
						})
					// })										
				},
				radioerrType(evt){
					this.formdata.BHTYPE==evt.target.value;
					console.log(this.formdata)
				},
				helpControlerr(){//纠错
					// uni.redirectTo({
					// 	url:"/pages/fxbhbtn/fxbhIndexErr?longitude="+_this.longitudeData+"&latitude="+_this.latitudeData
					// })
					if(document.getElementsByClassName('esri-popup__button').length>0){
						document.getElementsByClassName('esri-popup__button')[0].click();
					}
					this.errShow=true;
					this.showPageOne=false;
					this.showPageBars=false;
				},
				newaddBtn(){
					// uni.navigateTo({
					// 	url:"/pages/fxbhbtn/fxbhConten?current=0"
					// })
					this.current=0;
					this.getforInf();
					setInterval(_this.getTime(),1000);
					this.showPageOne=false;
					this.showPageBars=false;					
				},
				dismantleBtn(){
					this.current=1;
					this.getforInf();
					setInterval(_this.getTime(),1000);
					this.showPageOne=false;
					this.showPageBars=false;					
				},
				reformBtn(){
					this.current=2;
					this.getforInf();
					setInterval(_this.getTime(),1000);
					this.showPageOne=false;
					this.showPageBars=false;					
				},
				getforInf(){
					this.formdata.BHTYPE=this.radios[this.current].radioval;//radio值
					this.$jweixin.getLocation({
						type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
						  success: function (res) {
						    let latitude = res.latitude; 
						    let longitude = res.longitude;
							// _this.formdata.bhlocation="经度:"+longitude+"，纬度："+latitude;
						  },
						  fail:function(err){
						  	console.log(JSON.stringify(err));
						  }
					});	
				},
				getTime(){
					  let yy = new Date().getFullYear();
					  let mm = new Date().getMonth()+1;
					  let dd = new Date().getDate();
					  let hh = new Date().getHours();
					  let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
					  let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
					  _this.formdata.SUNTIME = yy+'/'+mm+'/'+dd+' '+hh+':'+mf;//时间
				},
				cameraChild(index){//照片
					uni.chooseImage({
					    count: 1, //默认9
					    sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					    sourceType: ['album'], //从相册选择
					    success: function (res) {
							_this.formdata.imgfiles[index].imagesrc=res.tempFiles[0].path;
							uni.uploadFile({//图片上传返回
								url: 'http://192.168.1.120:56273/api/photo/uploadphoto',
								filePath: res.tempFilePaths[0],
								name: "file",
								headers: {'Authorization':uni.getStorageSync('token')},
								success(res) {//获取照片
									_this.imgfilesNew[index]=JSON.parse(res.data).Data;
								}
							});						
					    }
					});
				},
				selectOne(){
					this.formdata.BHBEFORE=document.getElementById('selectOne').value;
				},
				selectTwo(){
					this.formdata.BHAFTER=document.getElementById('selectTwo').value;
				},
				drawRange(){
					this.showPageOne=true;					
					this.showPageBars=false;
					this.showPageThree=true;
					// uni.navigateTo({
					// 	url:"/pages/fxbhbtn/drawIndex"
					// })
				},
				drawEventClear(){
					_this.view.graphics.removeAll();
				},
				drawEvent(){
						var action =_this.draw.create("polygon");					
						action.on("vertex-add", function (evt) {
							_this.createPolygonGraphic(evt.vertices);
						});
						// action.on("vertex-remove", function (evt) {
						// 	createPolygonGraphic(evt.vertices);
						// });
						action.on("cursor-update", function (evt) {
							_this.createPolygonGraphic(evt.vertices);
						});
						action.on("draw-complete", function (evt) {
							_this.createPolygonGraphic(evt.vertices);
						});		
				},
				createPolygonGraphic(vertices){
					_this.view.graphics.removeAll();
					var polygon = {
					  type: "polygon", // autocasts as Polygon
					  rings: vertices,
					  spatialReference: _this.view.spatialReference
					};
					esriLoader.loadModules(["esri/Graphic"])
					  .then(([Graphic])=>{
						_this.addGraphic = new Graphic({
							geometry: polygon,
							symbol: {
							  type: "simple-fill", // autocasts as SimpleFillSymbol
							  color: [226, 119, 40,0.1],
							  style: "solid",
							  outline: {  // autocasts as SimpleLineSymbol
								color: "red",
								width: 1
							  }
							},
							attributes: _this.formdata
						});	
						_this.view.graphics.add(_this.addGraphic);
					 })
				    
				 },
				backContent(){
					this.showPageOne=false;
					this.showPageBars=false;
					_this.view.graphics.removeAll();					
				},
				submitImgs(){
					if(this.formdata.BHREMARK.length<15){
						uni.showToast({
							icon: 'none',
							position: 'bottom',
							title: '变化详情不能少于15个字'
						});
					}else{
						_this.map.layers.items[3].applyEdits({
						    addFeatures: [_this.addGraphic]
						}).then(res=>{
							_this.addId=res.addFeatureResults[0].objectId;						
							// 将id和图片链接集合提交
							request2({
								url:'/api/bhfx/submitphotos',
								method:"POST",
								header: {'Authorization': uni.getStorageSync('token')},
								data: {
									"objectId":_this.addId,
									"photos":_this.imgfilesNew
								},
							})
							.then((res)=>{
								if(res.data.Status=="success"){
									_this.map.layers.items[3].refresh();							
								}else{//提交失败删除
									_this.map.layers.items[3].applyEdits({
									    deleteFeatures: [_this.addGraphic]
									});
									uni.showModal({
										title: '提交信息',
										content:'提交失败，重新操作',
										showCancel: false,						
										confirmText: '关闭'
									})
								}
								_this.showPageOne=true;
								_this.showPageThree=false;
								_this.showPageBars=true;
							})
						});						
					}
				}				
			}
		}
</script>

